import { Fragment, useEffect } from 'react';
import { Box, styled } from '@mui/material'

import NavBars from './NavBar';
import Bennner from './benner';
import Slide from './Slide';

import { getProduct } from '../../redux/actions/productActions';
import { useDispatch ,useSelector} from 'react-redux';

const Component = styled(Box)`
padding: 10px 10px;
background:#f6f6f6;
`

const Home = () => {

   const {product} = useSelector(state => state.getProduct)

    const dispatch = useDispatch();
    console.log(product);

    useEffect(() => {
        dispatch(getProduct())
    }, [dispatch])

    return (
        <Fragment>
            <NavBars />
            <Component>
                <Bennner />
                <Slide product={product} title="Deal of the day" timer={true}/>
                <Slide product={product} title="Trending offer" timer={false}/>
                <Slide product={product} title="Season tops" timer={false}/>
                <Slide product={product} title="Best discount on Items" timer={false}/>
            </Component>

        </Fragment>

    )
}

export default Home;